@import '../../utils/breakpoints';

.chartOverview {
  &__navbar {
    display: flex;
    height: 40px;
    align-items: center;
    justify-content: space-between;
  }
  &__title {
    font-size: 22px;
    line-height: 125.3%;
    color: #192434;
  }
  &__filters {
    display: flex;
    justify-content: space-between;
    width: 88px;
  }
  &__filter {
    position: relative;
    border-radius: 4px;
    &:hover {
      background: rgba(28, 52, 84, 0.26);
    }
  }
  &__dateFilter {
    transform: translate(50%, 50px) !important;
  }
  &__content {
    min-height: 100%;
    display: flex; 
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    row-gap: 20px;
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__date {
    font-size: 13px;
    line-height: 125.3%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(28, 46, 69, 0.6);
  }
  &__money {
    font-weight: bold;
    font-size: 32px;
    line-height: 132.6%;
    color: rgba(24, 39, 58, 0.94);
    margin-top: 16px;
    &-statistic {
      margin-left: auto;
      margin-top: 0;
      font-size: 14px;
    }
    &-percent {
      color: rgba(28, 48, 74, 0.5);
      margin-left: 4px;
    }
  }
  &__percent {
    font-weight: 600;
    font-size: 22px;
    line-height: 132.6%;
    text-align: center;
    margin-left: 20px;
    color: rgba(28, 48, 74, 0.5);
  }
  .filter {
    &__icon {
      border-radius: 4px;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(25, 59, 103, 0.05);
    }
  }
}

@media (max-width: 900px) {
  .chartOverview {
    &__dateFilter {
      transform: translate(-30px, 50px) !important;
    }
  }
}

@media (max-width: 700px) {
  .chartOverview {
    &__dateFilter {
      transform: translate(0,0) !important;
    }
    &__filter {
      position: static;
    }
  }
}