@import '../../utils/breakpoints';

.chart {
  position: relative;
  margin: 28px;
  &__center {
    top: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__circle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 10px solid #d8dcfc;
    border-radius: 50%;
  }
  &__icon {
    cursor: pointer;
    transition: all 1s ;
    &:hover {
      transform: scale(1.1);
    }
  }
  &__iconTitle {
    margin-top: 8px;
    font-size: 8px;
    line-height: 125.3%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(27, 43, 65, 0.72);
  }
}


@media (max-width: $mobile-width) {
  .chart {
    margin: 0;
    margin-top: 20px;
 
  }
}