.transactionInfoModal {
  position: fixed;
  top: 0;
  left: 0;
  background: #FFFFFF;
  z-index: 100;
  box-shadow: 0px 4px 15px rgba(189, 194, 199, 0.4);
  width: 100%;
  height: 100%;
  font-size: 16px;
  line-height: 161.1%;
  color: #192434;
  &__header {
    padding: 40px 20px;
    height: 115px;
    background: linear-gradient(97.02deg, #8B75FF 1.89%, #6A89FF 100%);
    position: relative;
  }
  &__icon {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 70px;
    height: 70px;
    transform: translate(-50%, 50%);
  }
  &__info {
    padding-top: 43px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 2px;
  }
  &__date {
    font-size: 11px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(24, 39, 58, 0.94);
  }
  &__amountTitle {
    font-size: 13px;
    letter-spacing: 0.02em;
    text-transform: uppercase;  
    color: rgba(28, 46, 69, 0.6);
  }
  &__amount {
    font-size: 36px;
    line-height: 161.1%;
    color: #192434;
    transform: translateY(-15px);
    &-plus {
      &::before {
        margin-right: 2px;
        content: '+';
      }
    }
    &-minus {
      &::before {
        margin-right: 2px;
        content: '-'
      }
    }
  }
  &__tail {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
    padding: 0 20px;
  }
  &__trans {
    height: 60px;
    background: linear-gradient(0deg, rgba(25, 59, 103, 0.05), rgba(25, 59, 103, 0.05)), #FFFFFF;
    border-radius: 4px;
    padding: 11px 13px;
    display: flex;
    align-items: center;
    column-gap: 14px;
    font-size: 11px;
    line-height: 125.3%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(28, 48, 74, 0.5);
  }
  &__transInfo {
    display: flex;
    flex-direction: column;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 125.3%;
    color: #192434;
  }
  &__links {
    border-radius: 4px;
  }
  &__link {
    height: 60px;
    padding: 11px 13px;
    display: flex;
    align-items: center;
    column-gap: 14px;
    border: 1px solid rgba(26, 56, 96, 0.1);
  }
}