@import '../../utils/breakpoints';

.overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 700px));  
  grid-template-rows: minmax(100px, 298px) minmax(100px, 192px) minmax(100px, 389px);
  row-gap: 16px;
  column-gap: 16px;
  grid-auto-flow: row;
  &__item {
    border-radius: 4px;
    padding: 22px 16px 24px;
  }
  &__account {
    background: linear-gradient(97.02deg, #8B75FF 1.89%, #6A89FF 100%);
    color: white;
    font-size: 14px;
  }
  &__transfers {
    background: linear-gradient(180deg, rgba(25, 59, 103, 0.05) 0%, rgba(25, 59, 103, 0) 100%), #FFFFFF;
  }
  &__transactions {
    grid-row: 2/4;
    grid-column: 2/3;
  }
}


@media (max-width: 1170px) {
  .overview {
    grid-template-rows: minmax(100px, 298px) minmax(100px, 192px) minmax(100px, 550px);
    &__account {
      .balance {
        &__money {
          font-size: 26px;
        }
      }
    }
  }
}

@media (max-width: 900px) {
  .overview {
    // animation: openPageScaling ease-in-out 0.6s forwards;
    &__item {
      background: none;
      border: none;
      margin: 0 0;
    }
    grid-template-columns: auto;  
    grid-template-rows: 268px 104px 404px 438px;
    &__account {
      position: relative;
      background: linear-gradient(182.03deg, #8B75FF 12.49%, rgba(106, 137, 255, 0) 93.26%);
      border-radius: 20px;
      .account-details {
        &__title {
          font-size: 16px;
          line-height: 134%;
          color: #1E4DFC;
          position: absolute;
          bottom: 0;
          margin-top: 40px;
          left: 50%;
          transform: translateX(-50%);
        }
        &__content {
          margin: 0 16px;
        }
      }
      .balance {
        &__money {
          font-size: 34px;
        }
        &__currency {
          margin-left: 16px;
        }
      }
      .month-limit {
        padding: 9px 14px;
      }
    }
    &__promo {
      display: none;
    }
    &__transfers {
      .transfers-block {
        margin: 0 0;
        &__title {
          display: none;
        }
        &__options {
          margin: 0;
        }
      }
    }
    &__transactions {
      grid-row: auto;
      grid-column: auto;
      .transactions {
        &__navbar {
          justify-content: space-between;
        }
        &__search {
          display: none;
        }
      }
      .transaction {
        display: flex;
        align-items: center;
        margin-top: 10px;
        &:nth-last-child(-n+5) {
          opacity: 0.5;
        }
        &:nth-last-child(-n+1) {
          opacity: 0.3;
        }
      }
    }
  }
}

@media (max-width: 390px) {
  .overview {
    &__account {
      .balance {
        &__money {
          font-size: 26px;
        }
      }
    }
  }
}