@import '../../utils/breakpoints';

.transfers-block {
  height: 100%;
  &__title {
    font-size: 22px;
    line-height: 125.3%;
    color: #192434;
  }
  &__options {
    margin-top: 16px;
    display: grid;
    column-gap: 8px;
    height: 102px;
    grid-template-columns: repeat(3, 1fr);
  }
  &__option {
    cursor: pointer;
    display: flex;
    justify-content: center ;
    align-items: center;
    padding: 2px 0px 8px;
    flex-direction: column;
    position: static;
    box-sizing: border-box;
    box-shadow: 0px 4px 14px rgba(212, 221, 226, 0.7);
    border-radius: 4px;
    background: white;
    border: 1px solid rgba(26, 56, 96, 0.1);
    transition: 0.3s ease-in;
    &:hover {
      transform: scale(1.04);
      border: 1px solid #1E4DFC;
    }
  }
  .option {
    &__title {
      margin-top: 12px;
      font-size: 15px;
      line-height: 122%;
      color: rgba(24, 39, 58, 0.94);
    }
  }
}

@media (max-width: $mobile-width) {
  .transfers-block {
    .option {
      &__title {
        font-size: 10px;
      }
    }
  }
}