.transactions {
  height: 100%;
  overflow: hidden;
  position: relative;
  &__navbar {
    display: flex;
    height: 40px;
    align-items: center;
  }
  &__search {
    margin-left: auto;
    margin-right: 15px;
    max-width: 264px;
  }
  &__title {
    font-size: 22px;
    line-height: 125.3%;
    color: #192434;
  }
  &__more {
    text-decoration: none;
    color: #1E4DFC;
  }
  &__top {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    pointer-events: none;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 30%), rgba(255, 255, 255, 0.01);
  }
}

.empty {
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  &__content {
    margin-top: 40px;
    height: 100%;
    width: 100%;
    background: rgb(224, 224, 112);
    font-size: 28px;
    text-align: center;
    padding-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-filtered {
      background: rgb(144, 129, 224);
    }
  }
  &__info {
    color: #5a574c;
    &-filtered {
      color: white;
    }
  }
  &__icon {
    width: 100px;
    height: 100px;
    filter: invert(93%) sepia(46%) saturate(317%) hue-rotate(319deg) brightness(102%) contrast(93%);
  }
}