@import '../../utils/breakpoints';

.transaction-info {
  display: none;
  padding: 16px;
  background: rgba(25, 59, 103, 0.05);
  border-radius: 4px;
  margin: 16px 0px;
  &__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40px, auto));
  }
  &__block {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    color: rgba(24, 39, 58, 0.94);
    font-size: 13px;
  }
  &__arrow {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .block {
    &__title {
      font-size: 11px;
      line-height: 125.3%;
      letter-spacing: 0.03em;
      text-transform: uppercase;
      color: rgba(27, 43, 65, 0.72);
      &-big {
        font-size: 16px;
        color: rgba(24, 39, 58, 0.94);
      }
    }
    &__tail {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(20px, 60px));
      column-gap: 20px;
    }
    &__transfer {
      display: flex;
      flex-direction: column;
    }
    &__account {
      margin-top: 2px;
      font-size: 14px;
      line-height: 134%;
      color: rgba(24, 39, 58, 0.94);
    }
    &__money {
      margin-top: 2px;
    }
    &__status {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 125.3%;
      letter-spacing: -0.01em;
      color: #00A344;
      column-gap: 8px;
    }
  }
}

@media (max-width: $mobile-width) {
  // .transaction-info {
  //   display: none !important;
  // }
  .transaction-info {
    &__arrow {
      display: none;
    }
    &__container {
      grid-auto-flow: row;
      grid-template-columns: repeat(auto-fit, minmax(100px, auto));
      row-gap: 20px;
      column-gap: 20px;
      grid-template-rows: repeat(auto-fit, minmax(100px, auto));
    }
  }
}