.day-transactions {
  z-index: 1000;
  color: rgba(28, 46, 69, 0.6);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 44px;
  margin-top: 10px;
  &__date {
    text-transform: uppercase;
    font-size: 13px;
    line-height: 125.3%;
  }
  &__sum {
    font-size: 16px !important;
    line-height: 161.1%;
    color: rgba(28, 46, 69, 0.6) !important;
    &-plus {
      &::before {
        margin-right: 2px;
        content: '+';
      }
    }
    &-minus {
      &::before {
        margin-right: 2px;
        font-size: 16px;
        line-height: 161.1%;
        content: '—';
      }
    }
  }
}