.account-details {
  &__title {
    display: block;
    text-align: end;
    padding-right: 16px;
  }
  &__content {
    margin: 0 31px;
  }
}

.balance {
  margin-top: 19px;
  &__title {
    text-transform: uppercase;
    font-size: 13px;
    line-height: 125.3%;
  }
  &__money {
    margin-top: 8px;
    font-weight: 600;
    font-size: 48px;
    line-height: 125.3%;
    display: flex;
  }
  &__currency {
    margin-left: 24px;
  }
}

.month-limit {
  padding: 14px 25px;
  margin-top: 26px;
  background: rgba(28, 48, 74, 0.5);
  border-radius: 4px;
  &__progress {
    margin: 8px 0;
    border: 1px solid #15C15D;
    border-radius: 4px;
    height: 5px;
    width: 100%;
    &::-webkit-progress-bar {
      background-color: #F9A000;
    }
    &::-webkit-progress-value {
      background-color: #15C15D;
    }
  }
}
