@import '../../utils/breakpoints';

.transaction {
  display: flex;
  align-items: center;
  margin-top: 10px;
  z-index: 1000;
  &__info {
    margin-left: 10px;
  }
  &__title {
    color: rgba(24, 39, 58, 0.94)
  }
  &__method {
    font-size: 13px;
    color: rgba(27, 43, 65, 0.72);
  }
  &__sum {
    &-plus {
      &::before {
        margin-right: 2px;
        content: '+';
      }
      color: #00A344;
    }
    &-minus {
      &::before {
        margin-right: 2px;
        font-size: 16px;
        line-height: 161.1%;
        content: '—';
      }
      color: #F52419;
    }
  }
  &__tail {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__detail {
    margin-left: 20px;
    display: flex;
    align-items: center;
  }
  .detail {
    &__icon {
      border-radius: 50%;
      width: 32px;
      height: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background:rgba(24, 39, 58, 0.2)
      }
    }
  }
}

